<template>
  <label class="input_calc">
    <Transition name="slide-fade">
      <span
        class="error error-mrot"
        v-if="this.eaeu <= 23499 && !isDisableFotEaeu"
        >Внимание, вы указали ЗП меньше МРОТ. <br /><br />Минимальная заработная плата по Санкт-Петербургу 23 500 рублей.
      </span>
    </Transition>
    <span>ЕАЭС</span>
    <input
      type="number"
      min="0"
      v-model="eaeu"
      @input="updateValue"
      required
      placeholder="0"
      class="wage-level__patent"
    />
    
  </label>
</template>

<script>
export default {
  props: {
    isDisableFotEaeu: {
      type: Boolean,
    },
  },

  data() {
    return {
      eaeu: null,
    };
  },

  methods: {
    updateValue() {
      this.$emit("updateFotEaeu", this.eaeu);
    },
    resetValue() {
      this.eaeu = 0;
      this.$emit("updateFotEaeu", this.eaeu);
    },
  },
};
</script>

<style></style>
