<script>
export default {
  name: "table-vue",
  props: ["tablesList"],
  data() {
    return {
      fssProc: "1.80%",
      CONSTS: {
        FOT_FIX: 13890,
        NDFL: 0.13,
        NDS: 0.2,
        PFR_FIX: 0.22,
        PFR_REMAINDER: 0.1,
        FSS_INSURANCE: 0.018,
        FSS_OTHER: 0.029,
        FFOMC_FIX: 0.051,
        FFOMC_REMAINDER: 0.05,
      },
      tableListPatent: {},
      tableListEaeu: {},
      tableListRf: {},
      reglament: 0,
      reglamentRemain: 0,
      ffomc: 0,
      ffomcRemained: 0,
      fssC: 0,
      fssTrauma: 0,
      tax: 0,
      sumWithoutNdc: 0,
      ndc: 0,
      sumAndNdc: 0,
      emloyTd: 0,
      emloyTdMin: false,
      systemTax: null,
    };
  },

  methods: {
    checkFssProc() {
      if (this.tablesList.name !== "ПАТЕНТ") {
        this.fssProc = "2.90%";
      }
      if (this.tablesList.name == "ПАТЕНТ") {
        this.fssProc = "1.80%";
      }
    },
    getInnerHTML(table) {
      return table;
    },
    formated(value) {
      let formatter = new Intl.NumberFormat("ru", {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
      });
      return formatter.format(value);
    },
  },

  calc() {
      this.emloyTd = (
        this.tablesList.fot /
        (this.tablesList.hours * this.tablesList.days)
      ).toFixed(2);

      if (Number(this.emloyTd) <= 136) {
        this.emloyTdMin = true;
        this.emloyTd = 136;
      } else if (Number(this.emloyTd) > 136) {
        this.emloyTdMin = false;
      }
    },
  mounted() {
    this.$nextTick(function () {
      this.checkFssProc();
    });
  },
};
</script>

