<template>
  <label class="input_calc">
    <span>Патент</span>
    <input
      type="number"
      min="0"
      v-model="countPatent"
      @input="updateValue"
      :disabled="isDisableFotPatent"
      required
      placeholder="0"
      class="sum-staff__patent"
    />
    
    <span class="error"></span>
  </label>
</template>

<script>
export default {
  props: {
    isDisableFotPatent: {
      type: Boolean,
    },
  },

  data() {
    return {
      countPatent: null,
    };
  },

  methods: {
    updateValue() {
      this.$emit("updatePatent", this.countPatent);
    },
    resetValue() {
      this.countPatent = null;
      this.$emit("updatePatent", this.countPatent);
    },
  },
};
</script>

<style></style>
