<template>
  <label class="input_calc">
    <Transition name="slide-fade">
      <span
        class="error error-mrot"
        v-if="this.patent <= 23499 && !isDisableFotPatent"
        >Внимание, вы указали ЗП меньше МРОТ. <br /><br />Минимальная заработная плата по Санкт-Петербургу 23 500 рублей.</span
      >
    </Transition>
    <span>Патент</span>
    <input
      type="number"
      min="0"
      v-model="patent"
      @input="updateValue"
      required
      placeholder="0"
      class="wage-level__patent"
    />
    
  </label>
</template>

<script>
export default {
  props: {
    isDisableFotPatent: {
      type: Boolean,
    },
  },

  data() {
    return {
      patent: null,
    };
  },

  methods: {
    updateValue() {
      this.$emit("updateFotPatent", this.patent);
    },
    resetValue() {
      this.patent = 0;
      this.$emit("updateFotPatent", this.patent);
    },
  },
};
</script>

