<template>
  <label class="input_calc">
    <Transition name="slide-fade">
      <span class="error error-mrot" v-if="this.rf <= 23499 && !isDisableFotRf"
        >Внимание, вы указали ЗП меньше МРОТ. <br /><br />Минимальная заработная плата по Санкт-Петербургу 23 500 рублей.</span
      ></Transition
    >
    <span>РФ</span>
    <input
      type="number"
      min="0"
      v-model="rf"
      @input="updateValue"
      required
      placeholder="0"
      class="wage-level__patent"
    />
    
  </label>
</template>

<script>
export default {
  props: {
    isDisableFotRf: {
      type: Boolean,
    },
  },

  data() {
    return {
      rf: null,
    };
  },

  methods: {
    updateValue() {
      this.$emit("updateFotRf", this.rf);
    },
    resetValue() {
      this.rf = 0;
      this.$emit("updateFotRf", this.rf);
    },
  },
};
</script>

<style></style>
