<template>
  <label class="input_calc">
    <span>EAЭC</span>
    <input
      type="number"
      min="0"
      v-model="countEaeu"
      @input="updateValue"
      required
      placeholder="0"
      class="sum-staff__eaeu"
    />
    
    <span class="error"></span>
  </label>
</template>

<script>
export default {
  data() {
    return {
      countEaeu: null,
    };
  },

  methods: {
    updateValue() {
      this.$emit("updateEaeu", this.countEaeu);
    },
    initialValueOutsource() {
      this.countEaeu = 0;
      this.$emit("updateEaeu", this.countEaeu);
    },
    resetValueOutsource() {
      this.countEaeu = 0;
      this.$emit("updateEaeu", this.countEaeu);
    },
    resetValue() {
      this.countEaeu = 0;
      this.$emit("updateEaeu", this.countEaeu);
    },
  },
};
</script>

