<template>
  <label class="input_calc">
    <input
      type="number"
      min="0"
      v-model="countEaeu"
      @input="updateValue"
      :disabled="isDisableFotEaeu"
      required
      placeholder="0"
      class="sum-staff__eaeu"
    />
    <span>EAЭC</span>
    <span class="error"></span>
  </label>
</template>

<script>
export default {
  props: {
    isDisableFotEaeu: {
      type: Boolean,
    },
  },
  data() {
    return {
      countEaeu: 8,
    };
  },

  methods: {
    updateValue() {
      this.$emit("updateEaeu", this.countEaeu);
    },
    resetValue() {
      this.countEaeu = 8;
      this.$emit("updateEaeu", this.countEaeu);
    },
  },
};
</script>

